body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container-flex {
  display: flex;
}
.fd-row {
  flex-direction: row;
}
.fd-col {
  flex-direction: column;
}
.ai-center {
  align-items: center;
}
.jc-center {
  justify-content: center;
}
.padded {
  padding: 1em;
}
.padded-half {
  padding: 0.5em;
}
.padded-x2 {
  padding: 2em;
}
.padded-top {
  padding-top: 1em;
}
.padded-bottom {
  padding-bottom: 1em;
}
.padded-left {
  padding-left: 1em;
}
.padded-right {
  padding-right: 1em;
}

iframe {
  width: 100%;
  height: 100%;
  border: none;
  background: white;
}
